
.my-input {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 0;
  margin-bottom: 4px;
}
.my-input .left {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50%;
          flex: 0 0 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 50%;
}
.my-input .left input {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  font-size: 14px;
  height: 30px;
  border: 1px solid rgba(0,0,0,0.2);
}
.my-input .left .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  font-size: 29px;
  margin: 0 25px 0 10px;
}
.my-input .remove {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  line-height: 32px;
}
.my-input .remove span {
  font-size: 14px;
  margin-right: 15px;
}
.my-input .minus {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 20px;
          flex: 0 0 20px;
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 5px;
  border: 2px solid #4e4e4e;
  margin: 3px 20px 0px;
}
.my-input .minus:after {
  content: '  ';
  position: absolute;
  top: 50%;
  left: 50%;
  height: 2px;
  width: 80%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: #4e4e4e;
  border-radius: 1px;
}


