
.tab {
  width: 222px;
  height: 100%;
  color: #fff;
  background-color: #3d3d44;
}
.tab .tab-item {
  position: relative;
  text-align: center;
  height: 45px;
  line-height: 45px;
  cursor: pointer;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#45454d), to(#393940));
  background-image: linear-gradient(to bottom, #45454d, #393940);
  border-bottom: 1px solid rgba(255,255,255,0.1);
}
.tab .tab-item.router-link-active:before {
  content: '  ';
  position: absolute;
  top: 50%;
  right: 0;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-right-color: #fff;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}


