
.myHeader {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #000;
  color: #fff;
  padding: 19px 10px 30px;
}
.myHeader .brand {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
  font-size: 14px;
}
.myHeader .brand .big {
  margin-right: 10px;
  font-size: 22px;
}
.myHeader .information {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
  text-align: left;
  font-size: 16px;
  color: rgba(255,255,255,0.7);
}
.myHeader .information .working-time {
  margin-left: 30px;
}
.myHeader .sign-in-up {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: right;
  color: rgba(255,255,255,0.5);
}
.myHeader .sign-in-up .sign-in:hover,
.myHeader .sign-in-up .sign-up:hover {
  display: inline-block;
  color: #fff;
  cursor: pointer;
}
.myHeader .sign-in-up .sign-in {
  padding-right: 10px;
  border-right: 1px solid #fff;
}
.myHeader .sign-in-up .sign-up {
  padding-left: 10px;
}


